<template style="overflow:auto">
  <div id="NewsVideo" style="overflow:auto">
    <div style="height:0px"></div>
    <ul style="overflow:auto">

        
      <li
        v-for="(model,index) in datas"
        :key="index"
        class="videonewscell"
        @click="cellClick(model,index)"
      >
      <img :src="model.image" alt="" class="videoimageClass">
        <!-- <img src:{{model.image}} alt class="pdfImgClass" /> -->
      </li>
    </ul>
  </div>
</template>

<script>
import moreImg from "../../assets/more@3x.png";

export default {
  name: "NewsVideo",
  props: {
    title: {
      type: String,
      default: "nimeide"
    }
  },
  data() {
    return {
      titleOne: "APEX",
      datas: [],
      moreImg: moreImg
    };
  },
  methods: {
    clickAPEX: function() {
      console.log("3333");
      // this.datas = "2323322323";
      this.loadData(this);

      // this.$router.push({ name:'GQB'  })
    },
    loadData: function(obj) {
      $.ajax({
        url: "http://rap2api.taobao.org/app/mock/233798/video",
        type: "GET", //get
        dataType: "json", //json
        success: function(data) {
          //
          console.log(data);
          obj.datas = data.data;
          console.log(obj.datas);
          console.log("");

        }
      });
    },

    liClick: function(model) {
      alert(model.lastPrice);
    },
    functiontimetrans: function(date) {
      var date = new Date(date); //date131000

      var Y = date.getFullYear() + "-";

      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";

      var D =
        (date.getDate() < 10 ? "0" + date.getDate() : date.getDate()) + " ";

      var h =
        (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) + ":";

      var m =
        (date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes()) +
        ":";

      var s =
        date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();

      return Y + M + D + h + m + s;
    },

    cellClick: function(model, index) {
      // alert(model.title + index);
      // this.$router.push({name:"NewsDetail"});
      // this.$router.push({ name:'newsDetail'})

    //   window.location.href = model.url;
    }
  },

  mounted: function() {
    this.loadData(this);
  }
};
</script>


<style>
.divdata {
  /* padding-left: 4%; */
  /* padding-right: 4%; */
  /* width: 92%; */
  height: 68px;
  background-color: #282834;
  margin-bottom: 8px;
  margin-left: 15px;
  margin-right: 15px;
  border-radius: 8px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

#topView {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.NewsAnnouncementnewstitle {
  margin: 0;
  margin-top: 8px;
  margin: 5px 15px 5px 15px;
  height: 48px;
  line-height: 24px;
  color: #ffffff;
  font-size: 15px;
  /* width: 100%; */
  overflow: hidden;
  overflow-wrap: break-word;
  text-align: left;
}

.liulan {
  color: #7e829d;
  font-size: 11px;
  margin: 9px 0 21px 5.3%;
}

#mark {
  width: 100%;
  height: 24px;
  /* background-color: #fc3e30; */
  margin-top: 10px;
  margin-bottom: 6px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  justify-items: center;
}

#moreimg {
  width: 16px;
  height: 16px;
  margin: 4px 15px 0 0;
  text-align: right;
}

.videonewscell {
  width: 100%;
  text-align: left;
  position: relative;
  /* height: 136px; */
  overflow: hidden;
}

.videoimageClass{
    width: 94%;
    margin: 10px;
    border-radius: 10px;


}
</style>



