<template>
  <div id="root">
    <ul id="bottomul">
      <li v-for="(item,index) in datas" :key="index" class="bottomli" @click="cellClick(index)">
        <img v-if="!item.isSelect" :src="item.normalImg" alt class="image" />
        <img v-if="item.isSelect" :src="item.selectImg" alt class="image" />
        <p class="selectTitle" v-if="item.isSelect">{{item.title}}</p>
        <p class="title" v-if="!item.isSelect">{{item.title}}</p>
      </li>
    </ul>
  </div>
</template>

<script>
import HomeNormal from "../../assets/tabbar/home.png";
import HomeClick from "../../assets/tabbar/homeClick.png";
import DataNormal from "../../assets/tabbar/data.png";
import DataClick from "../../assets/tabbar//dataClick.png";
import NewsNormal from "../../assets/tabbar/news.png";
import NewsClick from "../../assets/tabbar/newsClick.png";
import ProductsNormal from "../../assets/tabbar/products.png";
import ProductsClick from "../../assets/tabbar/productsClick.png";
let model0 = {
  title: "Home",
  normalImg: HomeNormal,
  selectImg: HomeClick,
  isSelect: false
};
let model1 = {
  title: "Data",
  normalImg: DataNormal,
  selectImg: DataClick,
  isSelect: false
};
let model2 = {
  title: "News",
  normalImg: NewsNormal,
  selectImg: NewsClick,
  isSelect: false
};
let model3 = {
  title: "Products",
  normalImg: ProductsNormal,
  selectImg: ProductsClick,
  isSelect: false
};

export default {
  name: "RootName",
  props: {
    title: {
      type: String,
      default: "nimeide"
    },
    index: {
      type: String,
      default: "0"
    }
  },
  data() {
    return {
      titleOne: "APEX",
      datas: [model0, model1, model2, model3]
    };
  },
  methods: {
    clickAPEX: function() {
      console.log("3333");
      // this.$router.push({ name:'GQB'  })
    },
    cellClick: function(index) {
      if (index == 0) {
        this.$router.push({ name: "APEXHome" });
      } else if (index == 1) {
        this.$router.push({ name: "APEXData" });
      } else if (index == 2) {
        this.$router.push({ name: "APEXNews" });
      }else if (index == 3) {
        this.$router.push({ name: "APEXProducts" });
      }
    }
  },
  mounted: function() {
    model0.isSelect = false;
    model1.isSelect = false;
    model2.isSelect = false;
    model3.isSelect = false;
    if (this.index == "0") {
      model0.isSelect = true;
    } else if (this.index == "1") {
      model1.isSelect = true;
    } else if (this.index == "2") {
      model2.isSelect = true;
    } else if (this.index == "3") {
      model3.isSelect = true;
    }
  }
};
</script>


<style>
#root {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}
#bottomul {
  display: flex;
  justify-content: space-around;
  justify-items: flex-start;
  height: 49px;
  background-color: #21212b;
}
.bottomli {
  width: 25%;
  height: 49px;
}
.title {
  height: 13px;
  line-height: 13px;
  padding-top: 4px;
  margin: 0;
  color: #cdd5df;
  font-size: 11px;
}
.selectTitle {
  height: 13px;
  line-height: 13px;
  padding-top: 4px;
  margin: 0;
  color: #387cfe;
  font-size: 11px;
}
.image {
  width: 20px;
  height: 20px;
  padding-top: 8px;
}
</style>



