<template>
  <div id="homediv">
    <div style="height:64px"></div>
    <ul v-bind:class="{titleUlClass:true}">
      <li
        v-for="(item,index) in titles"
        :key="index"
        class="titleliClass"
        @click="titleClick(index)"
      >
        <div v-bind:class="{titledivClass:true}">{{item}}</div>
        <div class="lineClass" v-show="index == titleindex"></div>
      </li>
    </ul>
    <div class="bigbox">
      <NewsNews v-show="titleindex == 0"></NewsNews>
      <NewsAnnouncement v-show="titleindex == 1"></NewsAnnouncement>
      <NEWSVideo v-show="titleindex == 2"></NEWSVideo>
      <NewsAnnouncement v-show="titleindex == 3"></NewsAnnouncement>
      <div style="height:49px"></div>
    </div>

    <!-- <div style="height:49px"></div> -->

    <TopView @clicknow1="clicknow2" title="News"></TopView>
    <Bottom index="2"></Bottom>
  </div>
</template>

<script>
import TopView from "@/components/APEX/topView";
import Bottom from "./bottom";
import NewsNews from "./NewsNews";
import NewsAnnouncement from "./NewsAnnouncement";
import NEWSVideo from "./NEWSVideo";

export default {
  name: "HomeName",
  props: {
    title: {
      type: String,
      default: "nimeide"
    }
  },
  data() {
    return {
      titleOne: "News",
      titles: ["News", "Announcement", "Videos", "Research"],
      datas: [],
      titleindex: 0
    };
  },
  methods: {
    clickAPEX: function() {
      console.log("3333");
    },
    clicknow2: function() {
      alert("22");
    },
    loadData: function(obj) {
      $.ajax({
        url: "http://rap2api.taobao.org/app/mock/233798/hangqingdata",
        type: "GET", //get
        dataType: "json", //json
        success: function(data) {
          //
          console.log(data);
          obj.datas = data.data;
          console.log(obj.datas);
          console.log("");
        }
      });
    },
    titleClick: function(index) {
      alert(index);
      this.titleindex = index;
    }
  },
  components: {
    TopView,
    Bottom,
    NewsNews,
    NewsAnnouncement,
    NEWSVideo
  },

  mounted: function() {
    this.loadData(this);
  }
};
</script>


<style scoped>
#homediv {
  background-color: #21212b;
  width: 100%;
  padding: 0;
  margin: 0;
}

.titleUlClass {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  justify-items: center;
  position: fixed;

  width: 100%;
}

.titledivClass {
  color: #ffffff;
  font-size: 15px;
}
.titleliClass {
  display: flex;
  flex-direction: column;
  justify-items: flex-end;
  justify-content: center;
  text-align: center;
}
.lineClass {
  margin-top: 5px;
  height: 2px;
  top: 30px;
  background-color: #387cfe;
}

.bigbox {
  position: relative;
  right: 0;
  left: 0;
  top: 40px;
  height: 600px;
  /* width: 414px; */
  overflow: scroll;
  bottom: -49px;


}
</style>



