<template>
  <div id="homediv">
    <TopView @clicknow1="clicknow2" title="Data"></TopView>
    <!-- <div id="tempDiv"></div> -->
    <div class="topDiv"></div>
    <ul class="toptitle2">
      <li v-for="(item,index) in titles" :key="index" class="contractClass">
        <!-- {{item}} -->
        <div class="titlediv">{{item}}</div>
      </li>
    </ul>
    <div style="height:44px"></div>
    <ul v-bind:class="{volumeData:true}">
      <li v-for="(item,index) in datas" :key="index">
        <div v-bind:class="{divData:true}">
          <div class="div5">{{item.instrumentId}}</div>
          <div class="div6">{{item.lastPrice}}</div>
          <div class="div7">{{item.upDown}}</div>
          <div class="div8">{{item.volume}}</div>
        </div>
      </li>
    </ul>
    <div style="height:49px"></div>
    <Bottom index="1"></Bottom>
  </div>
</template>

<script>
import TopView from "@/components/APEX/topView";
import Bottom from "./bottom";

export default {
  name: "HomeName",
  props: {
    title: {
      type: String,
      default: "nimeide"
    }
  },
  data() {
    return {
      titleOne: "Data",
      titles: ["Contract", "Last", "Chg", "Volume"],
      datas: []
    };
  },
  methods: {
    clickAPEX: function() {
      console.log("3333");
    },
    clicknow2: function() {
      alert("22");
    },
    loadData: function(obj) {
      $.ajax({
        url: "http://rap2api.taobao.org/app/mock/233798/hangqingdata",
        type: "GET", //get
        dataType: "json", //json
        success: function(data) {
          //
          console.log(data);
          obj.datas = data.data;
          console.log(obj.datas);
          console.log("");
        }
      });
    }
  },
  components: {
    TopView,
    Bottom
  },

  mounted: function() {
    this.loadData(this);
  }
};
</script>


<style scoped>
#homediv {
  background-color: #21212b;
  width: 100%;
  padding: 0;
  margin: 0;
}

.toptitle2 {
  position: fixed;
  top: 44px;
  width: 100%;
  height: 44px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  justify-items: flex-start;
  /* padding-right: -15px; */
}

.titlediv {
  color: #7e829d;
  line-height: 40px;
  height: 40px;
  text-align: center;
  /* padding: 0 auto; */
}

.contractClass {
  width: 25%;
  height: 40px;
  text-align:left;
  background-color: #253557;
}

.topDiv {
  width: 100%;
  height: 44px;
}
.volumeData {
  display: flex;
  flex-direction: column;
  padding-right: 20px;
}

.divData {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  height: 50px;
  line-height: 50px;
  justify-items: center;
}

/* .divData div {
  color: #ffffff;
  width: 25%;
  height: 50px;
  text-align: right;
} */

.div5 {
  color: #ffffff;
  font-size: 16px;
  width: 25%;
  text-align: center;
}
.div6 {
  color: #fc3e30;
  font-size: 16px;
  width: 25%;
  text-align: center;
}
.div7 {
  color: #fc3e30;
  font-size: 16px;
  width: 25%;
  text-align: center;
}
.div8 {
  color: #ffffff;
  font-size: 16px;
  width: 20.8%;
  text-align: right;
  margin-right: 4.2%;
}
</style>



